@use 'base/variables' as v;

.alerta{

    padding: 1.5rem 1.5rem 1.5rem 3rem;
    text-transform: uppercase;
    margin: 1rem 0;
    font-size: 1.2rem;
    font-weight: v.$bold;
    text-align: center;
    &__error{
        background-color: lighten(v.$rojo,60%);
        border-radius: .5rem;
        border: 1px solid v.$rojo;
        border-left: 1rem solid v.$rojo;
        color: v.$rojo;

    }
    
    
    &__exito{
        background-color: lighten(v.$verde,50%);
        border-radius: .5rem;
        border: 1px solid v.$verde;
        border-left: 1rem solid v.$verde;
        color: darken(v.$verde,20%);
        
    }

}

